.containerParent {
  display: grid;
  grid-template-areas: ". container .";
  grid-template-columns: 7% 1fr 7%;
}
.containerChild {
  grid-area: container;
}
body {
  font-size: 20px;
  line-height: 22px;
  color: #474747;
}
body .header {
  width: 100%;
  display: grid;
  grid-template-areas: ". container .";
  grid-template-columns: 7% 1fr 7%;
}
body .header .container {
  grid-area: container;
  display: grid;
  grid-template-areas: ". headline headline" "logo search cabinet" "navPanel navPanel navPanel";
  grid-template-columns: 25% 1fr 25%;
}
body .header .container .header__headline {
  grid-area: headline;
}
body .header .container .header__headline p {
  font: 1em/2em "PTSans-Regular";
  color: #474747;
  text-align: center;
  background-color: #ececec;
  border-bottom-right-radius: 15px;
  border-bottom-left-radius: 15px;
}
body .header .container .header__logo {
  grid-area: logo;
}
body .header .container .header__logo img {
  width: 70%;
}
body .header .container .header__mainSearch {
  grid-area: search;
  display: flex;
  flex-wrap: wrap;
}
body .header .container .header__mainSearch .search {
  width: 100%;
  margin-top: 70px;
}
body .header .container .header__mainSearch .search input {
  width: 90%;
  height: 45px;
  padding-left: 25px;
  font: 1.125em/45px "PTSans-Regular";
  color: #9e9e9e;
  border: 3px solid #00929e;
  border-radius: 50px;
  outline: none;
}
body .header .container .header__mainSearch .search i {
  font-size: 1.5em;
  position: relative;
  left: -50px;
  top: 2px;
  color: #00929e;
}
body .header .container .header__mainSearch .numbers {
  margin-top: 50px;
}
body .header .container .header__mainSearch .numbers ul {
  display: flex;
}
body .header .container .header__mainSearch .numbers ul li {
  padding: 0 10px;
}
body .header .container .header__mainSearch .numbers ul li i {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 65px;
  height: 65px;
  font-size: 2em;
  color: #00929e;
  border: 3px solid #00929e;
  border-radius: 50px;
}
body .header .container .header__mainSearch .numbers ul li a {
  font: 1.6em/2em "PTSans-Regular";
  color: #474747;
  text-decoration: none;
}
body .header .container .header__mainSearch .workTime {
  margin: 55px 0 0 60px;
  display: flex;
}
body .header .container .header__mainSearch .workTime i {
  font-size: 1.5em;
  line-height: 1.725em;
  color: #00929e;
}
body .header .container .header__mainSearch .workTime ul {
  margin-left: 20px;
}
body .header .container .header__mainSearch .workTime ul li span {
  font: 0.7em/1em "PTSans-Regular";
}
body .header .container .header__cabinet {
  grid-area: cabinet;
  margin-top: 70px;
}
body .header .container .header__cabinet ul {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
body .header .container .header__cabinet ul li {
  display: flex;
  justify-content: space-around;
  align-content: center;
}
body .header .container .header__cabinet ul li span {
  display: inline-block;
  font: 1.125em/1.1em "PTSans-Regular";
  text-align: center;
}
body .header .container .header__cabinet ul li span i {
  padding: 0 20px;
  font-size: 2em;
  color: #00929e;
}
body .header .container .header__cabinet ul li span:hover {
  text-decoration: underline;
  cursor: pointer;
}
body .header .container .header__cabinet ul li:nth-child(1) {
  width: 45%;
}
body .header .container .header__cabinet ul li:nth-child(2) {
  width: 45%;
}
body .header .container .header__cabinet ul li:nth-child(3) {
  margin-top: 50px;
  padding: 10px;
  width: 310px;
  height: 50px;
  justify-content: space-between;
  align-items: center;
  border: 1px solid #00929e;
  border-radius: 5px;
}
body .header .container .header__cabinet ul li:nth-child(3) span i {
  margin: 0 0 0 15px;
  padding: 3px;
  color: #fff;
  background-color: #7d3daf;
  border-radius: 5px;
}
body .header .container .header__nav-panel {
  grid-area: navPanel;
}
body .header .container .header__nav-panel__major ul {
  display: flex;
  justify-content: space-between;
  margin-top: 50px;
}
body .header .container .header__nav-panel__major ul li {
  width: 16.67%;
}
body .header .container .header__nav-panel__major ul li a {
  display: inline-block;
  width: 100%;
  font: 1.125em/2em "PTSans-Bold";
  color: #fff;
  text-align: center;
  text-decoration: none;
  background-color: #176975;
  border-right: 1px solid #77c9cc;
  border-left: 1px solid #77c9cc;
}
body .header .container .header__nav-panel__major ul li a:hover {
  background-color: #9e79ac;
}
body .header .container .header__nav-panel__goods ul {
  display: flex;
  justify-content: space-between;
}
body .header .container .header__nav-panel__goods ul li {
  width: 25%;
}
body .header .container .header__nav-panel__goods ul li a {
  display: inline-block;
  width: 100%;
  font: 1.125em/2em "PTSans-Regular";
  color: #0e3a40;
  text-align: center;
  text-decoration: none;
  background-color: #77c9cc;
  border: 1px solid #00929e;
}
body .header .container .header__nav-panel__goods ul li a:hover {
  color: #fff;
  background-color: #9e79ac;
}
body .content {
  width: 100%;
  display: grid;
  grid-template-areas: ". container .";
  grid-template-columns: 7% 1fr 7%;
}
body .content .container {
  grid-area: container;
  display: grid;
  grid-template-areas: "aside catalog popProd" "order order order";
  grid-template-columns: 20% 1fr 20%;
}
body .content .container .content__aside {
  grid-area: aside;
}
body .content .container .content__catalog {
  grid-area: catalog;
}
body .content .container .content__popularProducts {
  grid-area: popProd;
}
body .content .container .content__popularProducts h5 {
  font: 1em/1.125em "PTSans-Bold";
  color: #0e3a40;
}
body .content .container .content__popularProducts ul li {
  width: 250px;
  height: 350px;
  text-align: center;
  border: 1px solid #e6e6e6;
  border-radius: 5px;
}
body .content .container .content__popularProducts ul li img {
  margin-top: 15px;
  width: 200px;
  height: 200px;
}
body .content .container .content__popularProducts ul li p {
  padding: 10px;
  font: 1em/1.125em "PTSans-Regular";
  color: #0e3a40;
  text-align: left;
}
body .content .container .content__popularProducts ul li .price {
  font: 1.25em/1.375em "PTSans-Bold";
}
body .content .container .content__popularProducts ul li .price span {
  font: 0.875em/1em "PTSans-Regular";
}
body .content .container .content__popularProducts ul li:hover {
  border-color: #176975;
  cursor: pointer;
}
body .content .container .content__howToMakeOrder {
  grid-area: order;
  margin: 60px 0;
  display: flex;
  justify-content: space-between;
}
body .content .container .content__howToMakeOrder div {
  width: 32.5%;
  height: 235px;
  display: flex;
  flex-wrap: wrap;
  background-color: #e6e6e6;
  border-radius: 5px;
}
body .content .container .content__howToMakeOrder div .icons {
  width: 40%;
  display: flex;
  justify-content: flex-end;
}
body .content .container .content__howToMakeOrder div .icons i {
  font-size: 2em;
  color: #1b6b77;
}
body .content .container .content__howToMakeOrder div .icons .arrow {
  margin-top: 30px;
  width: 20%;
  text-align: right;
}
body .content .container .content__howToMakeOrder div .icons .cart,
body .content .container .content__howToMakeOrder div .icons .box,
body .content .container .content__howToMakeOrder div .icons .truck {
  margin-top: 40px;
  width: 100px;
  height: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 2px solid #1b6b77;
  border-radius: 75px;
}
body .content .container .content__howToMakeOrder div .learnMore {
  width: 60%;
  display: flex;
  justify-content: center;
  align-items: center;
}
body .content .container .content__howToMakeOrder div .learnMore a {
  width: 165px;
  height: 45px;
  font: 0.875em/45px "PTSans-Regular";
  color: #bababa;
  text-align: center;
  text-decoration: none;
  border: 1px solid #bababa;
  border-radius: 5px;
}
body .content .container .content__howToMakeOrder div .learnMore a:hover {
  color: #fff;
  background-color: #1b6b77;
}
body .content .container .content__howToMakeOrder div .info {
  margin-left: 110px;
  width: 100%;
  color: #474747;
}
body .content .container .content__howToMakeOrder div .info span:nth-child(1) {
  font: 1em/1.125em "PTSans-Bold";
}
body .content .container .content__howToMakeOrder div .info span:nth-child(2) {
  font: 0.875em/1em "PTSans-Regular";
}
body .content__addres {
  display: flex;
}
body .content__addres iframe {
  width: 60%;
  height: 600px;
}
body .content__addres .addres__contactForm {
  width: 40%;
  display: flex;
  flex-wrap: wrap;
  background-color: #176975;
}
body .content__addres .addres__contactForm .contactForm__comentary {
  margin: 45px 0 0 45px;
}
body .content__addres .addres__contactForm .contactForm__comentary input {
  padding-left: 15px;
  font: 1.125em/45px "PTSans-Regular";
  color: #828282;
  border: 1px solid #176975;
  border-radius: 10px;
}
body .content__addres .addres__contactForm .contactForm__comentary input[type*="text"] {
  margin: 0 5px 10px;
  width: 245px;
  height: 45px;
}
body .content__addres .addres__contactForm .contactForm__comentary input[type*="textarea"] {
  width: 515px;
  height: 100px;
  resize: none;
}
body .content__addres .addres__contactForm .contactForm__comentary button {
  margin-top: 20px;
  width: 165px;
  height: 45px;
  font: 1.125em/40px "PTSans-Regular";
  color: #fff;
  text-align: center;
  background: transparent;
  border: 1px solid #fff;
  border-radius: 10px;
  outline: none;
}
body .content__addres .addres__contactForm .contactForm__comentary button:hover {
  cursor: pointer;
}
body .content__addres .addres__contactForm .contactForm__contacts {
  margin-left: 45px;
  width: 70%;
  display: flex;
  justify-content: space-between;
}
body .content__addres .addres__contactForm .contactForm__contacts p {
  font: 1.125em/1.25em "PTSans-Regular";
  color: #fff;
}
body .content__addres .addres__contactForm .contactForm__contacts p a {
  color: #fff;
  text-decoration: none;
}
body .content__addres .addres__contactForm .contactForm__contacts p a:hover {
  text-decoration: underline;
}
body .content__addres .addres__contactForm .contactForm__social {
  margin-left: 45px;
  width: 70%;
  display: flex;
  justify-content: space-between;
}
body .content__addres .addres__contactForm .contactForm__social ul {
  display: flex;
  justify-content: space-between;
}
body .content__addres .addres__contactForm .contactForm__social ul a {
  margin: 0 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50px;
  height: 50px;
  font-size: 1.5em;
  color: #176975;
  text-decoration: none;
  background-color: #fff;
  border-radius: 75px;
}
body .content__addres .addres__contactForm .contactForm__social p {
  color: #fff;
  text-align: right;
}
body .content__addres .addres__contactForm .contactForm__social p span:nth-child(1) {
  font: 0.875em/1em "PTSans-Regular";
}
body .content__addres .addres__contactForm .contactForm__social p span:nth-child(2) {
  font: 0.875em/1em "PTSans-Bold";
}
