@import "variables";
@import "mixin";

body {
  font-size: 20px;
  line-height: 22px;
  color: #474747;
  .header {
    width: 100%;
    .containerParent();
    .container {
      .containerChild();
      display: grid;
      grid-template-areas: ". headline headline"
                           "logo search cabinet"
                           "navPanel navPanel navPanel";
      grid-template-columns: 25% 1fr 25%;
      .header__headline {
        grid-area: headline;
        p {
          font: 1em/2em @regular;
          color: #474747;
          text-align: center;
          background-color: #ececec;
          border-bottom-right-radius: 15px;
          border-bottom-left-radius: 15px;
        }
      }
      .header__logo {
        grid-area: logo;
        img {
          width: 70%;
        }
      }
      .header__mainSearch {
        grid-area: search;
        display: flex;
        flex-wrap: wrap;
        .search {
          width: 100%;
          margin-top: 70px;
          input {
            width: 90%;
            height: 45px;
            padding-left: 25px;
            font: 1.125em/45px @regular;
            color: #9e9e9e;
            border: 3px solid @mainColor;
            border-radius: 50px;
            outline: none;
          }
          i {
            font-size: 1.5em;
            position: relative;
            left: -50px;
            top: 2px;
            color: @mainColor;
          }
        }
        .numbers {
          margin-top: 50px;
          ul {
            display: flex;
            li {
              padding: 0 10px;
              i {
                display: flex;
                justify-content: center;
                align-items: center;
                width: 65px;
                height: 65px;
                font-size: 2em;
                color: @mainColor;
                border: 3px solid @mainColor;
                border-radius: 50px;
              }
              a {
                font: 1.6em/2em @regular;
                color: #474747;
                text-decoration: none;
              }
            }
          }
        }
        .workTime {
          margin: 55px 0 0 60px;
          display: flex;
          i {
            font-size: 1.5em;
            line-height: 1.725em;
            color: @mainColor;
          }
          ul {
            margin-left: 20px;
            li {
              span {
                font: .7em/1em @regular;
              }
            }
          }
        }
      }
      .header__cabinet {
        grid-area: cabinet;
        margin-top: 70px;
        ul {
          display: flex;
          flex-wrap: wrap;
          justify-content: space-between;
          li {
            display: flex;
            justify-content: space-around;
            align-content: center;
            span {
              display: inline-block;
              font: 1.125em/1.1em @regular;
              text-align: center;
              i {
                padding: 0 20px;
                font-size: 2em;
                color: @mainColor;
              }
              &:hover {
                text-decoration: underline;
                cursor: pointer;
              }
            }
            &:nth-child(1) {
              width: 45%;
            }
            &:nth-child(2) {
              width: 45%;
            }
            &:nth-child(3) {
              margin-top: 50px;
              padding: 10px;
              width: 310px;
              height: 50px;
              justify-content: space-between;
              align-items: center;
              border: 1px solid @mainColor;
              border-radius: 5px;
              span {
                i {
                  margin: 0 0 0 15px;
                  padding: 3px;
                  color: #fff;
                  background-color: #7d3daf;
                  border-radius: 5px;
                }

              }
            }
          }
        }
      }
      .header__nav-panel {
        grid-area: navPanel;
        &__major {
          ul {
            display: flex;
            justify-content: space-between;
            margin-top: 50px;
            li {
              width: 16.67%;
              a {
                display: inline-block;
                width: 100%;
                font: 1.125em/2em @bold;
                color: #fff;
                text-align: center;
                text-decoration: none;
                background-color: #176975;
                border-right: 1px solid #77c9cc;
                border-left: 1px solid #77c9cc;
                &:hover {
                  background-color: @hoverColor;
                }
              }
            }
          }
        }
        &__goods {
          ul {
            display: flex;
            justify-content: space-between;
            li {
              width: 25%;
              a {
                display: inline-block;
                width: 100%;
                font: 1.125em/2em @regular;
                color: @linkColor;
                text-align: center;
                text-decoration: none;
                background-color: #77c9cc;
                border: 1px solid @mainColor;
                &:hover {
                  color: #fff;
                  background-color: @hoverColor;
                }
              }
            }
          }
        }
      }
    }
  }
  .content {
    width: 100%;
    .containerParent();
    .container {
      .containerChild();
      display: grid;
      grid-template-areas: "aside catalog popProd"
                           "order order order";
      grid-template-columns: 20% 1fr 20%;
      .content__aside{
        grid-area: aside;
      }
      .content__catalog{
        grid-area: catalog;
      }
      .content__popularProducts{
        grid-area: popProd;
        h5{
          font: 1em/1.125em @bold;
          color: @linkColor;
        }
        ul{
          li{
            width: 250px;
            height: 350px;
            text-align: center;
            border: 1px solid #e6e6e6;
            border-radius: 5px;
            img{
              margin-top: 15px;
              width: 200px;
              height: 200px;
            }
            p{
              padding: 10px;
              font: 1em/1.125em @regular;
              color: @linkColor;
              text-align: left;
            }
            .price{
              font: 1.25em/1.375em @bold;
              span{
                font: .875em/1em @regular
              }
            }
            &:hover{
              border-color: #176975;
              cursor: pointer;
            }
          }
        }
      }
      .content__howToMakeOrder {
        grid-area: order;
        margin: 60px 0;
        display: flex;
        justify-content: space-between;
        div {
          width: 32.5%;
          height: 235px;
          display: flex;
          flex-wrap: wrap;
          background-color: #e6e6e6;
          border-radius: 5px;
          .icons {
            width: 40%;
            display: flex;
            justify-content: flex-end;
            i {
              font-size: 2em;
              color: #1b6b77;
            }
            .arrow {
              margin-top: 30px;
              width: 20%;
              text-align: right;
            }
            .cart,
            .box,
            .truck {
              margin-top: 40px;
              width: 100px;
              height: 100px;
              display: flex;
              justify-content: center;
              align-items: center;
              border: 2px solid #1b6b77;
              border-radius: 75px;
            }
          }
          .learnMore {
            width: 60%;
            display: flex;
            justify-content: center;
            align-items: center;
            a {
              width: 165px;
              height: 45px;
              font: .875em/45px @regular;
              color: #bababa;
              text-align: center;
              text-decoration: none;
              border: 1px solid #bababa;
              border-radius: 5px;
              &:hover {
                color: #fff;
                background-color: #1b6b77;
              }
            }
          }
          .info {
            margin-left: 110px;
            width: 100%;
            color: #474747;
            span:nth-child(1) {
              font: 1em/1.125em @bold;
            }
            span:nth-child(2) {
              font: .875em/1em @regular;
            }
          }
        }
      }
    }
  }
  .content__addres {
    display: flex;
    iframe {
      width: 60%;
      height: 600px;
    }
    .addres__contactForm {
      width: 40%;
      display: flex;
      flex-wrap: wrap;
      background-color: #176975;
      .contactForm__comentary {
        margin: 45px 0 0 45px;
        input {
          padding-left: 15px;
          font: 1.125em/45px @regular;
          color: #828282;
          border: 1px solid #176975;
          border-radius: 10px;
        }
        input[type*="text"] {
          margin: 0 5px 10px;
          width: 245px;
          height: 45px;
        }
        input[type*="textarea"] {
          width: 515px;
          height: 100px;
          resize: none;
        }
        button {
          margin-top: 20px;
          width: 165px;
          height: 45px;
          font: 1.125em/40px @regular;
          color: #fff;
          text-align: center;
          background: transparent;
          border: 1px solid #fff;
          border-radius: 10px;
          outline: none;
          &:hover {
            cursor: pointer;
          }
        }
      }
      .contactForm__contacts {
        margin-left: 45px;
        width: 70%;
        display: flex;
        justify-content: space-between;
        p {
          font: 1.125em/1.25em @regular;
          color: #fff;
          a {
            color: #fff;
            text-decoration: none;
            &:hover {
              text-decoration: underline;
            }
          }
        }
      }
      .contactForm__social {
        margin-left: 45px;
        width: 70%;
        display: flex;
        justify-content: space-between;
        ul {
          display: flex;
          justify-content: space-between;
          a {
            margin: 0 5px;
            display: flex;
            justify-content: center;
            align-items: center;
            width: 50px;
            height: 50px;
            font-size: 1.5em;
            color: #176975;
            text-decoration: none;
            background-color: #fff;
            border-radius: 75px;
          }
        }
        p {
          color: #fff;
          text-align: right;
          span:nth-child(1) {
            font: .875em/1em @regular;
          }
          span:nth-child(2) {
            font: .875em/1em @bold;
          }
        }
      }
    }
  }
}

